<script>
import { CChartLineSimple, CChartBarSimple } from '../charts/index.js'
import axios from "axios" 
export default {
  name: 'WidgetsDropdown',
  components: { CChartLineSimple, CChartBarSimple },
  data() {
    return {
      ordersCount:220,
      arrayOfOrdersCount:[0,0],
      arrayOfOrdersCountStatus:[0,0,0],
      arrayOfOrdersCountBeta:[8,25,105,64,18,0,0],
      usersCount:0,
      usersCountBeta:120,
      arrayOfUsers:[],
      renderComponent:true,
      orderCountBeta:{"all":236,"review":182,"processing":22,"pending_payment":21,"completed":11},
      orderCount:{
        all:0,
        review:0,
        processing:0,
        pending_payment:0,
        completed:0
      }
    }
  },

  methods:{
    
    countUsers() {
      axios.get(`/admin/v1/users/all/count`).then(res=>{
        this.usersCount = res.data.data;
        this.arrayOfUsers[0] = res.data.data

      })
    },

    countOrders() {
      // axios.get(`/admin/v1/orders/count/all`).then(res=>{
      //   this.orderCount.all = res.data.data;

      // })

      axios.get(`/admin/v1/orders/count/review`)
      .then(res=>{
        this.orderCount.review = res.data.data

        this.orderCount.all += res.data.data;
        this.$nextTick(() => {
          // Add the component back in
          this.renderComponent = true;
        });
      })

      axios.get(`/admin/v1/orders/count/processing`)
      .then(res=>{
        this.orderCount.processing = res.data.data;
        this.arrayOfOrdersCountStatus[1] = res.data.data;
        this.orderCount.all += res.data.data;
        this.$nextTick(() => {
          // Add the component back in
          this.renderComponent = true;
        });
      })

      axios.get(`/admin/v1/orders/count/completed`)
      .then(res=>{
        this.orderCount.completed = res.data.data
        this.arrayOfOrdersCountStatus[2] = res.data.data;
        this.orderCount.all += res.data.data;
        this.$nextTick(() => {
          // Add the component back in
          this.renderComponent = true;
        });
      })

      axios.get(`/admin/v1/orders/count/pending_payment`)
      .then(res=>{
        this.orderCount.pending_payment = res.data.data
        this.arrayOfOrdersCountStatus[2] = res.data.data;
        this.orderCount.all += res.data.data;
        this.$nextTick(() => {
          // Add the component back in
          this.renderComponent = true;
        });
      })
    },

    getOrderBtween(from,to,x) {

      axios.get(`/admin/v1/orders/date/between/${from}/${to}`)
      .then(res=>{
        //this.arrayOfOrdersCount.push(res.data.data.length)
        this.arrayOfOrdersCount[x] = res.data.data.length;
        this.ordersCount += res.data.data.length;
        this.renderComponent = false;

        this.$nextTick(() => {
          // Add the component back in
          this.renderComponent = true;
        });
      })
    },

    loopOrdersCount() {
      let currentMonth   = new Date().getMonth() + 1
      let currentYear    = new Date().getUTCFullYear()
      let arrayOfMonthes = []
      let i = 1
      ///loop to get count of monthes
      do {
        if(i < 10) {
          arrayOfMonthes.push(String('0' + i))
        } else {
          arrayOfMonthes.push(String(i))
        }
        i++
      } while(i!= (currentMonth + 1))
      
      
      let y = 0

      for(i of arrayOfMonthes) {
        let from = `${currentYear}-${i}-01`
        let to   = `${currentYear}-${i}-30`
        this.getOrderBtween(from,to,y);

        y++
  
      }
    },
  },
  mounted() {

   // this.countOrders();
  

  }
}
</script>
